
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
html,
body {
	height: 100%;
}
body{
	font-size: 14px;
	line-height: 1.42857143;
}
h1{ font-size: 36px;
}
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	height: 100%;
}
.flex-vc{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.content-box {
	-webkit-box-flex: 1;
	    -ms-flex: 1 0 auto;
	        flex: 1 0 auto;
}
#footer {
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 auto;
	        flex: 0 0 auto;
}
.container-fluid{
	max-width: 1400px;
	width: 100%;
	margin: auto;
}
header .container-fluid>div{
	display: inline-block;
}
.header-top{
	background: #000;
	height: 35px;
	color: #fff;
}
.header-top a{
	color: #fff;
	text-decoration: none;
}
.header-top-section, .header-bottom-section{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	padding: 0 15px;
}
.header-top-section-menu a{
	padding-right: 30px;
}
.hbox-phone a{
	padding-right: 50px;
}
.hbox-phone .fa-icon{
	vertical-align: middle;
	margin-right: 5px;
	width: 18px;
	height: 18px;
}
.hbox-phone a{
	color: #fff;
	text-decoration: none;
	font-size: 16px;
}
.mini-cart img{
	vertical-align: bottom;
	margin-right: 5px;
	width: 18px;
	height: 18px;
}
.login-section a{
	cursor: pointer;
}
.header-bottom{
	height: 60px;
	position: relative;
}
#catalog-menu{
	padding-left: 60px;
	text-decoration: none;
	color: inherit;
}
#show-modal-findmoto img, #catalog-menu img{
	padding-right: 15px;
}
#show-modal-findmoto, #catalog-menu{
	cursor: pointer;
}
header #search_form input {
	color: #000;
}
.swiper-button-prev, .swiper-container-rtl .swiper-button-next{
	background-color: rgba(128,31,37,0.4);
}
.breadcrumbs{
	font-size: 11px;
	background: #801f25;
	height: 30px;
	color: #fff;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	padding-left: 20px;
	margin-bottom: 15px;
}
.padding-delmiter{
	padding: 0 5px;
}
.content-fluid{
	width: 1160px;
	margin: 30px auto;
}
.f-col{
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
}
footer{
	border-top: 13px solid #801f25;
}
.footer-top{
	background: #262626;
	color: #9b9b9b;
}
.footer-top .container-fluid{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	height: 68px;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.footer-top ul{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	width: 1160px;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	list-style: none;
	font-size: 12px;
}
.footer-top ul li:before{
	content: '\2022';
	font-size: 46px;
	vertical-align: middle;
	margin-bottom: 5px;
	display: inline-block;
	margin-right: 7px;
}
.footer-top a{
	text-decoration: none;
	color: inherit;
}
.footer-top li:hover a, .footer-top li:hover:before{
	color: #ccc;
	font-weight: 600;
}
.footer-bottom{
	background: #000;
	color: #787878;
}
.footer-bottom .container-fluid{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	height: 154px;
}
.fbox-c {
	-webkit-box-flex: 2;
	    -ms-flex: 2;
	        flex: 2;
	border-left: 1px solid #787878;
	border-right: 1px solid #787878;
	height: 100%;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}
.fbox-lr {
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	text-align: center;
	font-size: 12px;
}
.fbox-l-container{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	width: 180px;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}
.fbox-r-container{
	width: 180px;
}
.fbox-r-container span{
	clear: both;
	display: block;
	margin-top: 5px;
}
.copyright{
	font-size: 12px;
}
.fbox-c-phone{
	margin: 15px 0;
}
.fbox-c-phone .fa-icon{
	vertical-align: bottom;
	margin-right: 5px;
	width: 18px;
	height: 18px;
}
.fbox-c-phone a{
	color: #9b9b9b;
	text-decoration: none;
	margin: 0 15px;
	font-size: 16px;
}
.fbox-c-menu{
	margin: 15px 0;
}
.fbox-c-menu a{
	color: #9b9b9b;
	text-decoration: none;
	margin: 0 15px;
	font-size: 12px;
	cursor: pointer;
}
.fbox-c a:hover{
	color: #ccc;
}
footer #search_form input {
	background: #ccc;
}



